import Vue          from 'vue'
import Router       from 'vue-router'
import store        from '@/store'
import HelloWorld   from '@/components/HelloWorld'
import Portal       from '@/views/Portal'
import Registration from '@/views/Registration'
import Login        from '@/views/Login'
  import InputInvitation  from '@/components/InputInvitation'
  import RegInput         from '@/components/RegInput'
  import CompReg          from '@/components/CompReg'
import MyPage       from '@/views/MyPage'
  import MyProfile        from '@/components/MyProfile'
  import EditProfile      from '@/components/EditProfile'
  import UpdatePassword   from '@/components/PasswordUpdate'
import ProfileView  from '@/views/ProfileView'
import Inquiry      from '@/views/Inquiry'
import Members      from '@/views/Members'
import Events       from '@/views/Events'
import Event        from '@/views/Event'
import RegEvent     from '@/views/RegEvent'

  Vue.use(Router)

// export default new Router({
  const router = new Router({
  routes: [
    // ポータル
    {
      path: '/',
      name: 'Portal',
      component: Portal,
      meta: 
      {
        requiresAuth : true
      },
      beforeEnter:async function(to, from, next)
      {
        await store.dispatch('initPortalPage')
        next()
      }
    },

    //  会員登録
    {
      path: '/registration',
      component: Registration,
      meta:
      {
        title : "会員登録"
      },

      children:[
        {
          name: 'reg-inv',
          path: '',
          component: InputInvitation,
          meta:
          {
            title : "招待コード入力"
          }
        },
        {
          name: 'reg-input',
          path: 'input',
          component: RegInput,
          meta:
          {
            title : "会員登録"
          },
          beforeEnter(to, from, next)
          {
            store.dispatch('CheckInvitationCode')
            next()
          }
        },
        {
          name: 'reg-comp',
          path: 'comp',
          component: CompReg,
          meta:
          {
            title : "会員登録完了"
          },
          beforeEnter(to, from, next)
          {
            store.dispatch('Registration')
            next()
          }
        }
      ]

    },

    // ログイン
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta:
      {
        title : "ログイン"
      },

      // ログイン済みの場合、topへ
      beforeEnter(to, from, next)
      {
        if(store.getters.logined)
        {
          next('/')
        }
        else{
          next()
        }
      }
    },

    // マイページ
    {
      path : '/mypage',
      component: MyPage,
      meta: 
      {
        requiresAuth : true,
        title : "マイページ"
      },
      children:[
        // マイページTOP
        {
          name: 'MyPage',
          path: '',
          component: MyProfile,
          meta:
          {
            title : "マイページ"
          },

          beforeEnter(to, from, next)
          {
            store.dispatch('initMypage')
            next()
          }
        },
        // プロフィール編集
        {
          name: 'EditProfile',
          path: 'edit',
          component : EditProfile,
          meta:
          {
            title : "プロフィール編集"
          },

          // プロフィールデータ未読み込みの場合はマイページへ
          beforeEnter(to, from, next)
          {
            if(store.getters.profile.userId == null)
            {
              next('/mypage')
            }
            else{
              next()
            }
          }
        },
        // パスワード変更
        {
          name : 'UpdatePassword',
          path:'password',
          component : UpdatePassword,
          meta:
          {
            title : "パスワード変更"
          }
        }
      ]
    },

    // プロフィール
    {
      path : '/profile/:userNo',
      component: ProfileView,
      meta: 
      {
        requiresAuth : true,
        title : "プロフィール"
      },

      beforeEnter(to, from, next)
      {
        store.dispatch('initProfile', to.params.userNo)
        next()
      }
    },

    // お問い合わせフォーム
    {
      path : '/inquiry',
      component : Inquiry,
      meta:
      {
        requiresAuth : true,
        title : "お問い合わせフォーム"
      }
    },

    // メンバーリスト
    {
      path : '/members',
      component : Members,
      meta:
      {
        requiresAuth : true,
        title : "メンバーリスト"
      }
    },

    // イベントリスト
    {
      path : '/events',
      component : Events,
      meta:
      {
        requiresAuth : true,
        title : "イベント一覧"
      }
    },

    // イベント
    {
      path : '/events/detail/:eventNo',
      component : Event,
      meta:
      {
        requiresAuth : true,
        title : "イベント"
      }
    },

    // イベント登録
    {
      path : '/events/registration',
      component : RegEvent,
      meta:
      {
        requiresAuth : true,
        title : "イベント登録"
      }
    },


  ]
})

router.beforeEach((to, from, next) => async function(){
  store.dispatch('setShowSidebar', false)
  store.dispatch('setLoading', true)

  console.log('info:' + to.path)

  if(to.matched.some(record => record.meta.requiresAuth))
  {
    console.log('info:need auth')

    // ログインチェック
    await store.dispatch('checkLogin')

    // ログイン済み
    if(store.getters.logined)
    {
      store.dispatch('setLoading', false)
      console.log('info:logined')
      next()
      return
    }
    else{
      store.dispatch('setLoading', false)
      // 未ログイン
      console.log('info:not logined')
      next({name:'Login'})
    }
  }
  else{
    store.dispatch('setLoading', false)
    next()
  }
}())

router.afterEach((to,from) =>
  {
    if(to.meta.title != null)
    {
      document.title = to.meta.title + " - " + process.env.PageTitle
    }
    else
    {
      document.title = process.env.PageTitle + " - " + process.env.PageDesc
    }
    
  }
)

export default router


