<template>
    <section>
        <h1 data-v-events>イベント一覧</h1>
        <div style="margin-bottom:50px;">
            <h2 v-for="(event, index) in events" :key="index" data-v-events>
                <router-link v-bind:to="'/events/detail/' + event.no">{{ event.name }}</router-link>
                <h3 data-v-events>場所：{{event.pos.posName}}</h3>
                <h3 data-v-events>期間：{{event.time}}</h3>
            </h2>
        </div>
        <router-link to='/events/registration'><div class="buttonBase">イベント登録</div></router-link>
    </section>
</template>

<script>
export default {
    name : "Events",
    computed: {
        events() {
        return this.$store.getters.events;
        }
    },

    created()
    {
        this.$store.dispatch('initEventlist')
    },
        
    data: function () {
        return {
        }
    }
}   
</script>

<style scoped>
    h1[data-v-events] {
        font-size: 150%;
    }

    h2[data-v-events] {
        font-size: 120%;
        font-weight: normal;
        margin: 15px 0px 3px 10px;
    }

    h3[data-v-events] {
        font-size: 80%;
        font-weight: normal;
        margin: 3px 0px 0px 10px;
    }
</style>


