        <textarea rows="5" data-v-inquiry></textarea>

        <div class="buttonBase" v-on:click="toSend">送信</div>
    </section>
</template>

<script>
import Profile   from '@/components/Profile'
export default {
    name : "Inquiry",

    methods:{
        toSend(){}
    }
}
</script>

<style scoped>
    h1[data-v-inquiry] {
        font-size: 130%;
        font-weight: normal;
        color: #00a7ce;

        margin: 20px 0px 5px 0px;
    }

    h2[data-v-inquiry] {
        font-size: 100%;
        font-weight: normal;

        margin: 30px 0px 0px 10px;
    }

    input[data-v-inquiry]{
        margin: 5px 0px 0px 10px;
    }

    textarea[data-v-inquiry]{
        margin: 5px 0px 20px 10px;
    }
</style>



