<template>
    <div style="position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 200%;
        height: 150%;
        background-color: rgba(0, 0, 0, .5);
        display: table;"
        v-on:click="closeMenu">

        <div id="menu" data-v-sidemenuF>
            <!-- <div class="buttonBase" style="margin:20px 0px" v-on:click="closeMenu">Close</div> -->
            <h1 class="userId" data-v-sidemenuF>{{ userId }}</h1>
                <h2 data-v-sidemenuF><router-link to='/'>Portal</router-link></h2>
                <h2 data-v-sidemenuF><router-link to='/mypage'>マイページ</router-link></h2>
                <h2 data-v-sidemenuF><a href="#" v-on:click="logout">ログアウト</a></h2>

            <h1 data-v-sidemenuF><span class="logo">@</span><span class="logoTxt">TJC</span></h1>
                <h2 data-v-sidemenuF><router-link to='/members'>メンバーリスト</router-link></h2>
                <h2 data-v-sidemenuF><router-link to='/events'>イベント一覧</router-link></h2>
                <!-- <h2 data-v-sidemenuF><router-link to='/Inquiry'>お問い合わせフォーム</router-link></h2> -->
                
            <h1 data-v-sidemenuF>外部リンク</h1>
                <h2 data-v-sidemenuF><a href="https://gifukoujichikai.tumblr.com/" target="_blank">WEB自治会室</a></h2>
                <h2 data-v-sidemenuF><a href="https://tjc-word-library.herokuapp.com/libraries/" target="_blank">用語名言集</a></h2>

        </div>
    </div>
</template>

<script>
export default {
    name : 'SideMenu',

    computed : {
        userId()
        {
            return this.$store.getters.userInfo.userId
        },
    },

    data: function () {
        return {
            title: "test",
            menuList: [
                {
                    id: 1,
                    pageName: "Portal",
                    url: location.pathname + "?pId=portal",
                    layer: "1",
                    select: false,
                    del: false
                },
                {
                    id: 7,
                    pageName: "Financial Accountings",
                    url: null,
                    layer: "1",
                    select: false,
                    del: false
                },
                {
                    id: 8,
                    pageName: "家計簿入力",
                    url: location.pathname + "?pId=fa01",
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 9,
                    pageName: "Bank間振替",
                    url: location.pathname + "?pId=fa02",
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 10,
                    pageName: "月次締処理",
                    url: location.pathname + "?pId=fa03",
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 11,
                    pageName: "伝票検索",
                    url: location.pathname + "?pId=fa05",
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 12,
                    pageName: "レポート",
                    url: location.pathname + "?pId=fa07",
                    layer: "2",
                    select: false,
                    del: false
                },
            ]
        }
    },
    methods : {
        closeMenu(e){
            this.$store.dispatch('setShowSidebar', false)
        },
        nonClose(){},

        logout(){
            this.$store.dispatch('callLogoutApi')
            // window.alert("ログアウト")
        }
        
    }
}
</script>

<style scoped>
    #menu[data-v-sidemenuF]{
        /* margin : 15px; */
        background-color: #fafafa;
        height:150%;
        width:250px;

        /* margin-top:-10px; */
        padding:10px 15px;
    }

    #menu h1[data-v-sidemenuF]{
        font-size: 120%;
        font-weight: 500;
        margin: 25px 0px 5px 10px;
    }

    #menu h2[data-v-sidemenuF]{
        font-size: 100%;
        font-weight: 300;
        margin: 5px 0px 5px 30px;
    }

    .logo {
        color: #00a7ce;
        font-weight: bolder;
        /* font-size: 130%; */
        font-family: 'Open Sans', Segoe UI, "メイリオ", Meiryo, sans-serif;
    }
    .logoTxt {
        font-size: 110%;
    }

    /* #menu .selected[data-v-sidemenuF]{
        color: #f16272;
    } */
</style>





